/* ---- header ---- */
/* ---- header navi ---- */
#g-nav {
  /*position:fixed;にし、z-indexの数値を大きくして前面へ*/
  position: fixed;
  z-index: 999;
  /*ナビのスタート位置と形状*/
  top: 0;
  left: -120%;
  width: 400px;
  height: 100vh;
  /*ナビの高さ*/
  background-color: rgba(235, 89, 60, 0.8);
  /*動き*/
  transition: all 0.6s;
  /*スマホ 768px以下*/ }
  @media (max-width: 48em) {
    #g-nav {
      width: 100%;
      background-color: #eb593c; } }

/*アクティブクラスがついたら位置を0に*/
#g-nav.panelactive {
  left: 0; }

/*ナビゲーションの縦スクロール*/
#g-nav.panelactive #g-nav-list {
  /*ナビの数が増えた場合縦スクロール*/
  position: fixed;
  z-index: 999;
  width: 400px;
  height: 100vh;
  /*表示する高さ*/
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  /*スマホ 768px以下*/ }
  @media (max-width: 48em) {
    #g-nav.panelactive #g-nav-list {
      width: 100%; } }

/*ナビゲーション*/
#g-nav ul {
  /*ナビゲーション天地中央揃え*/
  position: absolute;
  z-index: 999;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  /*スマホ 768px以下*/ }
  @media (max-width: 48em) {
    #g-nav ul {
      left: 50%; } }

/*リストのレイアウト設定*/
#g-nav li {
  list-style: none;
  text-align: center; }

#g-nav li a {
  color: #FFF;
  text-decoration: none;
  padding: 10px;
  display: block;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  font-weight: bold; }

#g-nav li :hover {
  color: #1A8BAC;
  transition: all 0.3s ease-out; }

.g-nav_icon {
  background-image: url(/wp-content/themes/muso/img/common/brain_navi.svg);
  padding: 3rem;
  background-size: auto;
  background-position: center;
  background-repeat: no-repeat; }

/*========= ボタンのためのCSS ===============*/
:root {
  --bar-thickness: 5px; }

.hamburger_posi {
  /*スマホ 768px以下*/ }
  @media (max-width: 48em) {
    .hamburger_posi {
      position: relative;
      top: -35px;
      left: -40px; } }
  @media screen and (max-width: 320px) {
    .hamburger_posi {
      position: relative;
      top: -31px;
      left: -37px; } }

.hamburger {
  width: 30px;
  height: 25px;
  position: absolute;
  transition: transform .5s;
  transform: rotate(45deg);
  top: 50vh;
  left: 0;
  right: 0;
  margin: auto;
  /*スマホ 768px以下*/ }
  @media (max-width: 48em) {
    .hamburger {
      top: 0;
      left: 0px;
      right: auto; } }

.hamburger:hover {
  /*pc769px以上*/ }
  @media all and (min-width: 48.0625em) {
    .hamburger:hover {
      transform: rotate(-45deg);
      margin: auto;
      left: 0;
      right: 0; } }

.hamburger div {
  position: absolute;
  width: 100%;
  height: 4px;
  background-color: #FFF;
  border-radius: 3px;
  transform-origin: 50% 50%;
  transition: .5s .25s; }

.hamburger div:nth-child(1) {
  top: 0;
  width: 22px;
  left: 4px; }

.hamburger div:nth-child(2) {
  top: calc(50% - var(--bar-thickness) / 2);
  margin: auto;
  left: 0;
  right: 0; }

.hamburger div:nth-child(3) {
  top: calc(100% - var(--bar-thickness));
  width: 22px;
  left: 4px; }

.hamburger:hover div {
  /*pc769px以上*/ }
  @media all and (min-width: 48.0625em) {
    .hamburger:hover div {
      top: calc(50% - var(--bar-thickness) / 2); } }

.hamburger:hover div:nth-child(1) {
  /*pc769px以上*/ }
  @media all and (min-width: 48.0625em) {
    .hamburger:hover div:nth-child(1) {
      transform: scale(1, 0);
      transform-origin: 50% 50%;
      margin: auto;
      left: 0;
      right: 0; } }

.hamburger:hover div:nth-child(2) {
  /*pc769px以上*/ }
  @media all and (min-width: 48.0625em) {
    .hamburger:hover div:nth-child(2) {
      top: calc(50% - var(--bar-thickness) / 2);
      width: 30px;
      margin: auto;
      left: 0;
      right: 0; } }

.hamburger:hover div:nth-child(3) {
  /*pc769px以上*/ }
  @media all and (min-width: 48.0625em) {
    .hamburger:hover div:nth-child(3) {
      transform: scale(1, 0);
      transform-origin: 50% 50%; } }

.hamburger.header_active {
  transform: rotate(-45deg);
  margin: auto; }

.hamburger.header_active div {
  top: calc(50% - var(--bar-thickness) / 2);
  margin: auto;
  left: 0;
  right: 0; }

.hamburger.header_active div:nth-child(1) {
  transform: rotate(45deg);
  width: 31px;
  transform: rotate(90deg);
  background-color: #1A8BAC;
  transition: .5s .25s;
  margin: auto;
  left: 0;
  right: 0; }

.hamburger.header_active div:nth-child(2) {
  top: calc(50% - var(--bar-thickness) / 2);
  width: 30px;
  margin: auto;
  left: 0;
  right: 0; }

.hamburger.header_active div:nth-child(3) {
  opacity: 0; }

.openbtn1 {
  z-index: 9998;
  /*ボタンを最前面に*/
  top: 0px;
  left: 0px;
  cursor: pointer;
  width: 50px;
  transition: all 0.5s ease-out;
  /*スマホ 768px以下*/ }
  @media (max-width: 48em) {
    .openbtn1 {
      top: -50px;
      left: -50px;
      position: absolute;
      transform: rotate(-45deg); } }
  @media screen and (max-width: 320px) {
    .openbtn1 {
      top: -45px;
      left: -45px; } }

/*×に変化*/
.openbtn1 span {
  display: inline-block;
  transition: all .4s;
  position: absolute;
  left: 14px;
  height: 3px;
  border-radius: 2px;
  background-color: #FFF;
  width: 45%; }

.openbtn1 span:nth-of-type(1) {
