  position: relative;
  /*+マークの位置基準とするためrelative指定*/
  cursor: pointer;
  font-size: 0.8rem;
  font-weight: normal;
  padding: 1% 1% 1% 50px;
  transition: all .5s ease;
  margin: 0;
  font-weight: bold; }

/*アイコンの＋と×*/
.bn_accordion::before,
.bn_accordion::after {
  position: absolute;
  content: '';
  width: 13px;
  height: 2px;
  background-color: #333; }

.bn_accordion::before {
  top: 48%;
  left: 15px;
  transform: rotate(0deg);
  transition: all 0.5s; }

.bn_accordion::after {
  top: 48%;
  left: 15px;
  transform: rotate(90deg);
  transition: all 0.5s; }

/*　closeというクラスがついたら形状変化　*/
.bn_accordion.close::before {
  transform: rotate(45deg);
  transition: all 0.5s; }

.bn_accordion.close::after {
  transform: rotate(-45deg);
  transition: all 0.5s; }

